<template>
  <div align="center">
    <v-row style="margin-top: 50px" justify="center">
      <div
        style="
          width: 100%;
          margin-top: 50px;
          position: fixed;
          top: 0;
          margin: auto;
          background-color: white;
          z-index: 9999;
          border-bottom: 1px solid black;
        "
      >
        <div style="width: 300px">
          <v-select
            @change="changeeventindex()"
            v-model="eventindex"
            placeholder="이벤트 차시 선택"
            :items="selectitem"
            :item-text="selectitem.text"
            :item-value="selectitem.value"
          ></v-select>
        </div>
        <div style="padding: 25px">
          <v-btn outlined style="background-color: green; color: white"
            >당첨 확인</v-btn
          >
          <v-btn
            outlined
            style="margin-left: 10px; background-color: red; color: white"
            >차단 확인</v-btn
          >
        </div>
      </div>
    </v-row>
    <table
      style="
        text-align: center;
        border-spacing: 0;
        border: 1px solid black;
        margin-top: 180px;
        margin-bottom: 200px;
        min-width: 1200px;
      "
    >
      <tr>
        <td id="tabletdtitle">핸드폰 번호</td>
        <td id="tabletdtitle">이름</td>
        <td id="tabletdtitle">메시지 내용</td>
        <td id="tabletdtitle">이벤트 차시</td>
        <td id="tabletdtitle">참여 날짜</td>
        <td id="tabletdtitle" style="width: 80px">당첨 관리</td>
        <td id="tabletdtitle" style="width: 80px">차단 관리</td>
      </tr>
      <tr v-for="(j, i) in temp_messages" :key="i">
        <td id="tabletd">
          {{ temp_messages[i].phoneNum }}
        </td>
        <td id="tabletd">
          {{ temp_messages[i].name }}
        </td>
        <td id="tabletd">
          {{ temp_messages[i].message }}
        </td>
        <td id="tabletd">{{ temp_messages[i].index }}차 이벤트</td>
        <td id="tabletd">
          {{ temp_messages[i].date }}
        </td>
        <td id="tabletd">
          <div class="wrapper" style="margin-top: 20px; margin-left: 3px">
            <input
              @click="selectaward(temp_messages[i].phoneNum)"
              v-model="temp_messages[i].isSelected"
              type="checkbox"
              class="switch"
              :id="`awardswitch${i}`"
            />
            <label :for="`awardswitch${i}`" class="switch_label">
              <span class="onf_btn"></span>
            </label>
          </div>
        </td>
        <td id="tabletd">
          <div class="wrapper" style="margin-top: 20px; margin-left: 3px">
            <input
              @click="selectblock(temp_messages[i].phoneNum)"
              v-model="temp_messages[i].block_status"
              type="checkbox"
              class="switch"
              :id="`blockswitch${i}`"
            />
            <label :for="`blockswitch${i}`" class="switch_label">
              <span class="onf_btn"></span>
            </label>
          </div>
        </td>
      </tr>
    </table>
  </div>
</template>

<script>
import * as api from "../../api/api-list";

export default {
  data() {
    return {
      messages: [],
      temp_messages: [],
      awardflaglist: [],
      awardtemp: [],
      blockflaglist: [],
      reverseblockflaglist: [],
      awardlist: [],
      cutlist: [],
      eventindex: 0,
      selectitem: [
        { text: "전체", value: 0 },
        { text: "1차 이벤트", value: 1 },
        { text: "2차 이벤트", value: 2 },
        { text: "3차 이벤트", value: 3 },
      ],
    };
  },
  async created() {
    const res = await api.getmessages();
    this.messages = res.data.result;
    console.log(this.messages);
    this.temp_messages = res.data.result;
    this.setawardflag(this.messages);
    this.setblockflag(this.messages);
  },
  methods: {
    setawardflag(arr) {
      for (let i = 0; i < arr.length; i++) {
        this.awardflaglist.push(arr[i].isSelected);
      }
    },
    setblockflag(arr) {
      for (let i = 0; i < arr.length; i++) {
        this.blockflaglist.push(arr[i].block_status);
      }
    },
    awardmsg() {
      // let arr = [];
      // console.log(this.awardflaglist);
      // for (let i = 0; i < this.awardflaglist.length; i++) {
      //   if (this.awardflaglist[i] == false) {
      //     continue;
      //   } else {
      //     arr.push(i);
      //   }
      // }
      // let awardarr = [];
      // for (let i = 0; i < arr.length; i++) {
      //   awardarr.push(this.messages[arr[i]]);
      // }
      // console.log(awardarr);
      api.awardmessage({ arr: this.awardlist });
      this.awardlist = [];
      // this.messages = [];
    },
    blockmsg() {
      //let arr = this.blockflaglist;
      //api.cutmessage({ arr });
      api.cutmessage({ arr: this.cutlist });
      this.cutlist = [];
    },
    selectaward(data) {
      if (this.awardlist.includes(data)) {
        for (let i = 0; i < this.awardlist.length; i++) {
          if (this.awardlist[i] === data) {
            this.awardlist.splice(i, 1);
            i--;
          }
        }
      } else {
        this.awardlist.push(data);
      }
      console.log(this.awardlist);
    },
    selectblock(data) {
      if (this.cutlist.includes(data)) {
        for (let i = 0; i < this.cutlist.length; i++) {
          if (this.cutlist[i] === data) {
            this.cutlist.splice(i, 1);
            i--;
          }
        }
      } else {
        this.cutlist.push(data);
      }
      console.log(this.cutlist);
    },
    changeeventindex() {
      console.log(this.eventindex);
      let arr = [];

      if (this.eventindex == "0") {
        this.temp_messages = this.messages;
        return;
      }

      for (let i = 0; i < this.messages.length; i++) {
        if (this.messages[i].index.toString() == this.eventindex.toString()) {
          arr.push(this.messages[i]);
        } else {
          continue;
        }
      }
      this.temp_messages = arr;
    },
  },
};
</script>

